$main: #38241B;
$active: #951205;
$starColor: gray;


.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200px;
    height: 200px;
}

nav {
    height: 62px;
    background: $main;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 5;

    .imgContainer {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .navLink {
        display: flex;
        align-items: center;

        button {
            cursor: inherit;
            color: white;
            background: none;
            border: none;

            &:not(:last-child) {
                margin-right: 24px;
                font-size: 18px;
            }

            &:last-child {
                font-size: 20px;
                padding: 6.5px 25px;
                background: $active;
                border-radius: 25px;
            }
        }

    }


}



.one {
    padding: 62px 0;
    height: 180vh;
    background: url('./images/bg2.png');
    background-attachment: fixed;
    // background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    position: relative;
    contain: paint;
    // z-index: -1;

    .oneContainer {
        position: sticky;
        top: 15%;
        background: url("./images/bg1.png");
        height: 597px;
        width: 1213px;

        .title {
            position: absolute;
            top: 51px;
            left: 50px;
            display: flex;

            h1 {
                font-size: 80px;
                font-weight: 800;
                color: $main;
                width: 320px;
                height: 104px;
                margin-right: 9px;
            }

            div {
                color: white;
                width: 109px;
                height: 46px;
                background: $active;
                border-radius: 14px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 34px;
                // font-family: 'Roboto';
                font-weight: 700;
                margin-top: 25px;
            }
        }

        .text1 {
            position: absolute;
            width: 343px;
            height: 180px;
            left: 53px;
            top: 177px;
            font-weight: 900;
            font-size: 42px;
            line-height: 60px;

            color: $main;
        }

        .text2 {
            position: absolute;
            width: 289px;
            height: 140px;
            left: 53px;
            top: 372px;
            font-weight: 400;
            font-size: 24px;
            line-height: 35px;
            color: $main;
        }

        .oneImg {
            position: absolute;
            right: -30px;
            top: -30px;
        }

        .text3 {
            position: absolute;
            right: 61px;
            top: 215px;

            .item {
                display: flex;
                align-items: center;

                div {
                    color: $main;

                    &:first-child {
                        width: 140px;
                        height: 31px;
                        font-weight: 500;
                        font-size: 28px;
                        line-height: 31px;
                        margin-right: 18px;
                    }

                    &:last-child {
                        display: flex;
                        align-items: center;
                        font-size: 28px;

                        span {
                            width: 109px;
                            height: 57px;
                            font-family: 'EB Garamond';
                            font-style: normal;
                            font-weight: 800;
                            font-size: 50px;
                            text-align: end;
                            margin-bottom: 10px;
                        }

                    }
                }
            }
        }

        .oneImg2 {
            position: absolute;
            width: 672.27px;
            height: 562px;
            left: 308px;
            // top: 36px;
            bottom: -562px;

        }

        .paperTop {
            position: absolute;
            width: 1280px;
            height: 720px;
            top: -25%;
            transform: translateY(-100px);
        }

        .paperDownLeft {
            position: absolute;
            width: 942px;
            height: 1058px;
            bottom: -100%;
            left: -20%;
        }

        .paperDownRight {
            position: absolute;
            width: 1218px;
            height: 1008px;
            bottom: -90%;
            right: -45%;
        }
    }
}

.two {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;

    .twoLogo {
        margin-top: 100.49px;
    }

    .twoText1 {
        margin-top: 42.17px;
        font-weight: 500;
        font-size: 43px;
        line-height: 62px;
        text-align: center;
        letter-spacing: 0.6em;
        color: $main;
    }

    .underLine {
        width: 100%;
        height: 9px;
        margin-top: 40px;
        border-top: 3px solid $main;
        border-bottom: 3px solid $main;
        margin-bottom: 99px;

    }

    .twoItem {
        background: url('./images/twoItemBg.png');
        width: 1200px;
        height: 528px;

        &:not(:last-child) {
            margin-bottom: 42px;

        }


        .itemNav {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            padding: 19px 40px 0px 40px;

            div:first-child {
                color: white;
                font-weight: 700;
                font-size: 44px;
            }
        }

        .itemContainer {
            margin-top: 66px;
            margin-left: 105.97px;
            display: flex;

            .twoItemImgContainer {
                margin-right: 86.65px;
                width: 313.38px;
                height: 290px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }

            .itemText {
                h1 {
                    font-family: Noto Serif TC;
                    font-size: 44px;
                    font-weight: 900;
                    line-height: 63px;
                    color: $main;
                }

                p {
                    font-family: 'Noto Sans TC';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 24px;
                    line-height: 35px;
                    width: 592px;
                    height: 70px;
                    color: $main;
                }

                .buttonContainer {
                    margin-top: 80px;

                    button {
                        width: 183px;
                        height: 70px;
                        border-radius: 70px;
                        background: transparent;
                        font-weight: 350;
                        font-size: 28px;
                        line-height: 41px;

                        &:last-child {
                            background: $active;
                            color: white;
                            border: none;
                            margin-left: 20px;
                        }
                    }
                }
            }
        }
    }
}

.three {
    margin-top: 175px;
    padding-bottom: 40px;

    h1 {
        width: 100%;
        text-align: center;
        height: 170;
        font-family: 'Noto Serif TC';
        font-style: normal;
        font-weight: 900;
        font-size: 60px;
        line-height: 170 px;
        color: #FFFFFF;
        background: $main;
    }

    .threeOne {
        padding-top: 92px;
        display: flex;
        flex-direction: column;

        .up,.down {
            display: flex;
            width: 100%;
            padding: 0 37px;

            .item {
                width: 25%;
                display: flex;
                flex-direction: column;
                align-items: center;
                div {
                    &:first-child {
                        font-size: 30px;
                        line-height: 43px;
                        color: $main;
                    }

                    &:nth-child(2) {
                        width: 48.53px;
                        height: 48.53px;
                        background: $main;
                        transform: rotate(45deg);
                        margin: 25px 0;
                    }

                    &:nth-child(3) {
                        font-size: 24px;
                        line-height: 35px;
                        height: 82px;
                        display: flex;
                        align-items: center;
                        color: $main;
                    }
                }

                &.active {
                    div {
                        &:nth-child(2) {
                        background: $active;

                        }
                    }
                }
            }
        }
    }
}